(function() {
  'use strict';

  angular
    .module('documents', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .controller('TutorialsController', TutorialsController);

  /** @ngInject */
  function TutorialsController(){
  	var vm = this;

  	vm.init = function()
  	{};
  }
})();
(function() {
  'use strict';

  angular
    .module('documents')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($document, $window, $location) {
    var vm = this,
        __dialog;

    vm.blog_url = 'http://blog.ihsanberahim.com';

    window.dataLayer = [];
    window.dataLayer.push({
      virtualPageURL: $location.url(),
      virtualPageTitle: $window.document.title
    });

    vm.init = function()
    {
      __dialog = $window.document.getElementById('coming-soon-dialog');

      (function(buttons)
      {
        angular.forEach(buttons, function(_elem)
        {
          angular.element(_elem).on('click', function()
          {
            vm.doShowDialog();
          });
        });
      }($window.document.querySelectorAll('.show-coming-soon-dialog')));
    };
    vm.doShowDialog = function()
    {
      __dialog.showModal();
      __dialog.querySelector('.close').addEventListener('click', function() {
        __dialog.close();
      });
      __dialog.querySelector('.agree').addEventListener('click', function() {
        $window.open(vm.blog_url, '_self');
      });
    };
    vm.doCloseDialog = function()
    {
      __dialog.close();
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('documents')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html'
      })
      .when('/tutorials', {
        templateUrl: 'app/tutorials/tutorials.html',
        controller: 'TutorialsController',
        controllerAs: 'tutorials'
      })
      .when('/tutorials/:tutorial_name', {
        templateUrl: 'app/tutorials/tutorials.html',
        controller: 'TutorialsController',
        controllerAs: 'tutorials'
      })
      .otherwise({
        redirectTo: '/'
      });

    $locationProvider.html5Mode(true);
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('documents')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('documents')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

